time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

@keyframes moveIcons {
  0% {
    transform: rotate(55deg);
    left: 10%;
    opacity: 10%;
  }
  50% {
    transform: rotate(55deg);
    left: 80%;
    color: #f9f871;
  }
  100% {
    transform: rotate(-55deg);
    left: 10%;
  }
}
html {
  margin: 0;
}

body {
  padding: 0;
  margin: 0;
}

.main-header {
  width: 100%;
}

.main-nav {
  color: #fff;
  padding: 0;
  margin: 0;
  width: 100%;
  position: fixed;
  display: flex;
  z-index: 20;
  margin-top: 20px;
}
.main-nav ul {
  display: flex;
  justify-content: space-between;
  margin-right: 50px;
  margin-left: auto;
}
.main-nav ul li a {
  text-decoration: none;
  display: inline-block;
  color: #2F4858;
  background: #CEFBE3;
  padding: 5px 20px;
  border-radius: 5px;
  margin: 0 15px;
  font-family: "Roboto", sans-serif;
  transition: all 2s ease;
}
.main-nav ul li a:hover {
  background: linear-gradient(to right top, #f9f871, #b1e57a, #71cd87, #38b290, #039590);
  color: #23233B;
}

.wrapper {
  height: 100vh;
  width: 100%;
}

.showcase {
  margin: 0;
  width: 100%;
  height: 100vh;
  background: #23233B;
  z-index: 10;
  position: relative;
}
.showcase .showcase-content {
